        opacity: 0;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        margin: 0;
        z-index: -1;
    }

.radio__box {
        display: inline-block;
        vertical-align: middle;
        position: relative;
        border-radius: 50%;
        -webkit-transition: all 0.1s 0s ease-out;
        transition: all 0.1s 0s ease-out;
    }

.radio__box:before, .radio__box:after {
            content: '';
            display: block;
            position: absolute;
            background: transparent;
            border-radius: 50%;
            top: 50%;
            left: 50%;
            -webkit-transform: translate(-50%, -50%);
                    transform: translate(-50%, -50%);
        }

.radio__box:before {
            z-index: 2;
            opacity: 0;
            width: 100%;
            height: 100%;
            background: white;
            -webkit-transition: all 0.1s 0s ease-out;
            transition: all 0.1s 0s ease-out;
        }

